@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* Global styles start here */

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

body {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background: #09161d;
}
/* Global styles end here */

/* Product card general styles start here */
.card {
    position: relative;
    width: 320px;
    height: 420px;
    background: #122936;
    border-radius: 20px;
    overflow: hidden;
}

.card::before {
    content: '';
    position: absolute;
    top: -50%;
    width: 100%;
    height: 100%;
    background: #2196f3;
    -webkit-transform: skewY(345deg);
            transform: skewY(345deg);
    transition: 0.5s;
}

.card:hover::before {
    top: -50%;
    -webkit-transform: skewY(375deg);
            transform: skewY(375deg);
}

.card::after {
    content: 'SONY';
    position: absolute;
    bottom: 0;
    left: 12.5%;
    font-weight: 600;
    font-size: 5.5em;
    color: rgba(0,0,0,0.1);
}
/* Product card general styles end here */

/* Product card inner styles start here */
.card .img-area {
    position: relative;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 20px;
    z-index: 1;
}

.card .img-area img {
    max-width: 95%;
    transition: 0.5s;
}

.card:hover .img-area img {
    max-width: 70%;
}

.card .content-area {
    position: relative;
    padding: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    z-index: 1;
}

.card .content-area h3 {
    font-size: 18px;
    color: #fff;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 1px;
}

.card .content-area .price {
    font-size: 24px;
    color: #fff;
    font-weight: 500;
    letter-spacing: 1px;
    margin-bottom: .5em;
}
/* Product card inner styles end here */

/* Buy section styles start here */
.card .content-area .buy {
    position: relative;
    top: 200px;
    opacity: 0;
    font-size: 14px;
    padding: 5px 5px;
    margin-top: .7em;
    color: #fff;
    text-decoration: none;
    background: #2196f3;
    border-radius: 30px;
    text-transform: uppercase;
    letter-spacing: 1px;
    transition: 0.5s ease;
    display: flex;
    justify-content: center;
}

.card:hover .content-area .buy {
    top: 0;
    opacity: 1;
    font-size: 14px;
}
/* Buy section styles end here */

/* quantity component styles start here */
.quantity {
    display: flex;
    color: #fff;
    letter-spacing: 1px;
}

.amount {
    margin-left: .4em;
    margin-right: .3em;
}

.btn-quantity {
    width: 25px;
    height: 25px;
    padding-top: .1em;
    margin-left: .3em;
}
/* quantity component styles end here */
